@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

* {
  box-sizing: border-box;
}

:root {
  --orange: #f87719;
  --grey: #b7b7b7;
  --white: #fff;
  --black: #000;
  --background: #111;
  --secondary-text-grey: #f8f8f8;
  --secondary-black-grey: #282828;
  --grey-white-theme: #595959;
  color: var(--white);
  font-synthesis: none;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

body {
  background-color: var(--background);
  height: 100%;
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

.rbt-regular {
  font-weight: 400;
}

.rbt-medium {
  font-weight: 500;
}

.rbt-bold {
  font-weight: 700;
}

.container {
  max-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}

.container .rbt-bold {
  font-size: 40px;
}

@media (min-width: 768px) and (max-width: 1280px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }

  .rbt-bold {
    font-size: 52px;
  }
}

@media (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 32px;
  }

  .rbt-bold {
    font-size: 64px;
  }
}

